import commonHead from '../../components/commonHead.vue';
export default {
	data() {
		return {
			history:[],
			found:[
				{content:'Redmi 9 五星高品质',sort:'',url:''},
				{content:'Redmi 9 五星高品质',sort:'',url:''},
				{content:'Redmi 9 五星高品质',sort:'',url:''},
				{content:'Redmi 9 五星高品质',sort:'',url:''},
				{content:'Redmi 9 五星高品质',sort:'',url:''},
			]
		}
	},
	components:{
		commonHead,
	},
	onShow(){
		this.history = uni.getStorageSync('search') || [];
	},
	methods: {
		search(keyword){
			let search = uni.getStorageSync('search') || [];
			search.unshift(keyword);
			search = Array.from(new Set(search));
			uni.setStorage({
			    key: 'search',
			    data: search,
			});
			uni.navigateTo({
				url:`/pages/searchResult/searchResult?keyword=${keyword}`
			});
		},
		clearHistory(){
			const that = this;
			uni.showModal({
			    content: '是否删除全部历史',
			    success: function (res) {
			        if (res.confirm) {
			            that.history = [];
			            uni.removeStorage({
			                key: 'search',
			            });
			        }
			    }
			});
			
		}
	}
}
